.card {
  width: 80px;
  height: 100px;
  margin: 10px;
}


.card-flipped {
	background: -webkit-gradient(
    linear, 
    left top, left bottom, 
    from(rgb(255, 255, 255)), 
    to(rgb(255, 255, 255))
  );
	background: -webkit-linear-gradient(
    rgb(255, 255, 255), 
    rgb(255, 255, 255)
  );
	background: -o-linear-gradient(
    rgb(255, 255, 255), 
    rgb(255, 255, 255)
  );
	background: linear-gradient(
    rgb(255, 255, 255), 
    rgb(255, 255, 255)
  )
}


.card-layout {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
    width: 640px;
}

.App {
  height: 100vh;
}

.card-box {
  width: 640px;
  height: 80%;
}

.button-row {
  max-width: 300px;
}



